* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --index: calc(1vw + 1vh);
  --transition: 1.5s cubic-bezier(.05, .5, 0, 1);
}

@font-face {
  font-family: kamerik-3d;
  src: url("kamerik205-heavy.9d528567.woff2");
  font-weight: 900;
}

@font-face {
  font-family: merriweather-italic-3d;
  src: url("merriweather-regular-italic.ffb80337.woff2");
}

body {
  color: #fff;
  background-color: #000;
  margin: -120px -100px -100px;
  font-family: kamerik-3d;
}

a {
  text-decoration: none;
}

.logo {
  width: 300px;
  height: 300px;
  left: 50%;
  top: calc(var(--index) * 12);
  z-index: 1;
  background-repeat: no-repeat;
  background-size: contain;
  transition: all 1s;
  position: absolute;
  transform: translateX(-50%);
}

.logo:hover {
  width: 333px;
  height: 333px;
  transition: all 1s;
}

.layers {
  perspective: 1000px;
  overflow: hidden;
}

.layers__container {
  height: 100vh;
  min-height: 500px;
  transform-style: preserve-3d;
  transform: rotateX(var(--move-y) ) rotateY(var(--move-x) );
  will-change: transform;
  transition: transform var(--transition);
}

.layers__item {
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: -5vw;
  bottom: -5vw;
  left: -5vw;
  right: -5vw;
}

.layer-1 {
  transform: translateZ(-55px)scale(1.06);
}

.layer-2 {
  transform: translateZ(80px)scale(.88);
}

.layer-3 {
  transform: translateZ(180px)scale(.8);
}

.layer-4 {
  transform: translateZ(190px)scale(.9);
}

.layer-5 {
  transform: translateZ(300px)scale(.9);
}

.layer-6 {
  transform: translateZ(380px);
}

.hero-content {
  font-size: calc(var(--index) * 2.9);
  text-align: center;
  letter-spacing: calc(var(--index) * -.15);
  margin-top: calc(var(--index) * 5.5);
  line-height: 1.35em;
  transition: all 1s;
}

.hero-content span {
  display: block;
}

.hero-content__p {
  text-transform: none;
  letter-spacing: normal;
  font-family: merriweather-italic-3d;
  font-size: calc(var(--index) * .73);
  margin-top: 300px;
  line-height: 3;
}

.button {
  background-color: var(--iris);
  color: #fff;
  cursor: pointer;
  border: 5px;
  border-radius: 5px;
  padding: 15px 30px;
  font-size: 20px;
  transition: font-size 1s;
  position: relative;
  overflow: hidden;
}

.button:before {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0), rgba(255, 255, 255, .2), rgba(0, 0, 0, 0));
  animation: wave-animation 2s linear infinite;
  position: absolute;
  top: 0;
  left: -100%;
}

.button:hover {
  color: gold;
  border-color: #000;
  font-size: 30px;
  text-decoration: underline;
  transition: font-size 1s;
}

@keyframes wave-animation {
  0% {
    left: -100%;
  }

  100% {
    left: 100%;
  }
}

.container {
  max-width: 100%;
  padding: 0 15px;
}

.layer-4, .layer-5, .layer-6 {
  pointer-events: none;
}

